.about{
    background:$light-gray;
    padding: $xl-size+$sl-size $m-size;
    @media(max-width:$desktop-breakpoint){        
        padding:$xl-size+$ml-size $s-size;
    }
}
.about__body{
    display: flex;
    flex-direction: row;
    justify-content: center;
    @media(max-width:$desktop-breakpoint){        
        flex-direction: column;
    }
}
.about_me__text{
    text-align: center;
    font-size: 1.9rem;
    align-self: center;
    @media(max-width:$desktop-breakpoint){        
        text-align: center;
        margin-top: $l-size;
        font-size:$s-size+$sl-size;
    }
}
.my-img{
   margin: auto $xl-size;
   @media(max-width:$desktop-breakpoint){        
    margin:auto;
}
}
.my-img img{
    border-radius: 50%;
    box-shadow: 0 0 1rem #000000;
   
}


