.works{
    margin: 0 auto;
    margin-bottom: 0;
    background: -moz-linear-gradient(125deg, $gradient1-dark , #00838F,$gradient1-light ); /* ff3.6+ */
    background: -webkit-linear-gradient(125deg, $gradient1-dark ,#00838F, $gradient1-light ); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, $gradient1-dark , #00838F,$gradient1-light ); /* opera 11.10+ */
    background: -ms-linear-gradient(125deg, $gradient1-dark , #00838F,$gradient1-light ); /* ie10+ */
    background: linear-gradient(125deg, $gradient1-dark , #00838F,$gradient1-light); /* w3c */
    //  background: url('/images/bg4.jpg');
    padding-bottom: $l-size;
}

.works__title{
    text-align: center;
    font-weight:bold;
    font-size: $l-size;
    color:white;
    padding-top: $xl-size+$sl-size;
}

.works__subtitle{
    text-align: center;
    font-size: $ml-size;
    color: white;
}
.works__body{
    padding: 0;
    margin-top:$m-size;
    color: white;
    padding-bottom: $s-size;
}


