.modal{
    border-radius: 0% !important;
    margin: auto !important;
}
.modal-header{
    background: #4E342E;
}
.modal-title{
    color:white;
    font-size:$m-size;
}
.modal-body{
    padding: 0;
}
.close{
    color:white;
    font-size:$ml-size;
    &:hover{
        color:white;
    }
}
.modal-footer{
    padding:1rem;
}
.footer-button{
    font-size: 1.4rem;
}

.project-desc{
    padding: $m-size;
    font-size:$m-size;
}
.stack{
    font-size:$m-size;
    padding: 0 $m-size $s-size $m-size;
}
.badge{
    color:white;
    background: blue;
    padding: 0.5rem;
    margin:0.25rem 0.25rem;
    font-size:1.25rem;
    border-radius: 0;
}
.carousel-control-prev{
    background: black;
    height:5rem;
    width: 5rem;
    margin: auto $m-size;
    border-radius: 50%;
    &:hover{
        background: rgba(0,0,0,0.75);
    }
    @media(max-width:$desktop-breakpoint){        
       margin: auto $sl-size;
       height:3.5rem;
       width:3.5rem;
    }
}
.carousel-control-next{
    background: black;
    height:5rem;
    width: 5rem;
    margin: auto $s-size;
    border-radius: 50%;
    &:hover{
        background: rgba(0,0,0,0.75);
    }
    @media(max-width:$desktop-breakpoint){        
        margin: auto $sl-size;
        height:3.5rem;
        width:3.5rem;
     }
}


