.micro{
    margin: $ml-size $s-size;
    margin-top:$xl-size !important;
    box-shadow: 0px 0px 2rem #000000;
    
    background: -moz-linear-gradient(125deg, $gradient5-dark , $gradient5-light); /* ff3.6+ */
    background: -webkit-linear-gradient(125deg, $gradient5-dark , $gradient5-light); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, $gradient5-dark, $gradient5-light); /* opera 11.10+ */
    background: -ms-linear-gradient(125deg, $gradient5-dark, $gradient5-light); /* ie10+ */
    background: linear-gradient(125deg, $gradient5-dark , $gradient5-light); /* w3c */
    
    padding: $m-size $l-size;
    text-align: center;
    z-index: 50;
    position: relative;
    @media(max-width:$desktop-breakpoint){        
        margin:$l-size 2.4rem;
        padding: $m-size $ml-size;
    }
}

.micro__title{
    font-weight:bold;
    font-size: $ml-size;
    color:white;
    padding-top: $s-size;
}

.micro__subtitle{
    font-size: $m-size;
    color: white;
    margin-bottom: $m-size;
    padding: 0;
}

.micro__item{
    padding:$m-size;
    padding-top:$m-size; 
}
.micro__item_title{
    font-weight: 600;
    font-size: 1.8rem;
    margin-top: $sl-size;
    margin-bottom:$s-size;
}

.micro__card{
    height:100%;
    border: none;
    color: black;
    box-shadow: 0px 0px 0.5rem #000000;
    border-radius: 0;    
}
.micro__card__footer{
    margin:1rem $s-size;
    margin-top:0.75rem;
    padding: $sl-size;    
    border:0.11rem solid $micro-button-color;
    background: $micro-button-color;
    color:white;        
    border-color:white;
    font-size: 1.4rem;
    font-weight: 500;
    &:hover{
        background: white;
        border-color: $micro-button-color;
        color:$micro-button-color;
        font-weight: 700;
    }
    cursor: pointer;
}


