form{
    max-width:45rem;
    font-size:$m-size;
    margin: $l-size auto;
    @media(max-width:$desktop-breakpoint){ 
        margin:$l-size $l-size;
    }
}
.form-control{
    font-size:$m-size;
}
#send{
    margin-top:$s-size;
    font-size:1.7rem;
    color:white;
    border-color: white;
    &:hover{
      color:black;  
    }
}


