.android{
    margin: $ml-size $s-size;
    margin-top:$l-size !important;
    box-shadow: 0px 0px 2rem #000000;
    background: -moz-linear-gradient(125deg, $gradient2-dark, $gradient2-light); /* ff3.6+ */
    background: -webkit-linear-gradient(125deg, $gradient2-dark, $gradient2-light); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, $gradient2-dark, $gradient2-light); /* opera 11.10+ */
    background: -ms-linear-gradient(125deg, $gradient2-dark, $gradient2-light); /* ie10+ */
    background: linear-gradient(125deg, $gradient2-dark, $gradient2-light); /* w3c */
    padding: $m-size $l-size;
    text-align: center;
    z-index: 50;
    position: relative;
    @media(max-width:$desktop-breakpoint){        
        margin:$ml-size 2.4rem;
        padding: $m-size $ml-size;
    }

}

.android__title{
    font-weight:bold;
    font-size: $ml-size;
    color:white;
    padding-top: $s-size;
}

.android__subtitle{
    font-size: $m-size;
    color: white;   
    margin-bottom: $m-size;
    padding: 0;
}

.android__item{
    padding:$m-size;
    padding-top:$m-size; 
}
.android__item_title{
    font-weight: 600;
    margin-bottom: $s-size;
    font-size: 1.8rem;
}

.android__card{
    height:100%;
    border: none;
    color: black;
    box-shadow: 0px 0px 0.5rem #000000; 
    border-radius: 0;
}


.android__img{
    width:45%;
    align-self: center;
    margin-top: $ml-size;
    margin-bottom: $s-size;
    @media(max-width:$desktop-breakpoint){        
        width: 40%;
    }
}
.android__card__footer{
    margin:1rem $s-size;
    margin-top:0.75rem;
    padding: $sl-size;    
    border:0.11rem solid $android-button-color;
    background: $android-button-color;
    color:white;        
    border-color:white;
    font-size: 1.4rem;
    font-weight: 500;
    &:hover{
        background: white;
        border-color: $android-button-color;
        color:$android-button-color;
        font-weight: 700;
    }
    cursor: pointer;
}


