.creative{
    margin: $ml-size $s-size;
    margin-top:$xl-size !important;
    box-shadow: 0px 0px 2rem #000000;
    background: -moz-linear-gradient(125deg, $gradient4-dark , $gradient4-light); /* ff3.6+ */
    background: -webkit-linear-gradient(125deg, $gradient4-dark , $gradient4-light); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, $gradient4-dark, $gradient4-light); /* opera 11.10+ */
    background: -ms-linear-gradient(125deg, $gradient4-dark, $gradient4-light); /* ie10+ */
    background: linear-gradient(125deg, $gradient4-dark , $gradient4-light); /* w3c */
    padding: $m-size $l-size;
    text-align: center;
    z-index: 50;
    position: relative;
    padding-bottom: $l-size;
    @media(max-width:$desktop-breakpoint){        
        margin:$l-size 2.4rem;
        padding: $m-size $ml-size;
        padding-bottom: $l-size !important; 
    }
}

.creative__title{
    font-weight:bold;
    font-size: $ml-size;
    color:white;
    padding-top: $s-size;
}

.creative__subtitle{
    font-size: $m-size;
    color: white;
    margin-bottom: 0 !important;
    padding: 0;
}

.creative__item{
    padding:$m-size;
    padding-top:$m-size; 
}
.creative__item_title{
    font-weight: 700;
    font-size: 1.7rem;
    padding-top:0.25rem;
    padding-bottom:0.25rem;
    margin: 0 auto;
}
.creative__item_title2{
    font-weight: 600;
    margin: $s-size auto;
    margin-bottom: $s-size;
    font-size: 1.8rem;
}

.creative__card{
    height:100%;
    border: none;
    color: black;
    box-shadow: 0px 0px 0.5rem #000000;  
    border-radius: 0;  
}
.creative__card a{
    color:black;
    text-decoration: none;
}
.creative__item .card-body{
    padding: 0.75rem;
}

.creative__body__part{
    margin-top:0;
}
.creative__body__part__title{
    font-weight: 700;
    text-align: left;
}
.creative__body__part__subtitle{
    text-align: left;
}

.creative__part__button{
    margin-top:$m-size !important;
    font-size:$m-size;
    color:white;
    border-color: white;
    &:hover{
      color:black;  
    }
    cursor: pointer;
}
.download-span{
    font-size:1.4rem;
    color:#1976D2; 
    font-weight: 100 !important;
    
}
.learning__card__footer{
    margin:1rem $s-size;
    margin-top:0.75rem;
    padding: $sl-size;    
    border:0.11rem solid $learning-button-color;
    background: $learning-button-color;
    color:white;        
    border-color:white;
    font-size: 1.4rem;
    font-weight: 500;
    &:hover{
        background: white;
        border-color: $learning-button-color;
        color:$learning-button-color;
        font-weight: 700;
    }
    cursor: pointer;
}


