.skillset{
    padding-top: 5rem !important;
    padding-bottom: 5rem !important;
    padding-bottom: 0.1rem;
    margin: 0 $m-size !important;
    @media(min-width:$desktop-breakpoint){             
        margin: 0 11rem !important;
        padding-top: 6.5rem !important;
        padding-bottom: 7.5rem !important;   
    }
}

.skillset__title{
    text-align: center;
    font-weight:bold;
    font-size: $l-size;
    color:$dark-grey;
}

.skillset__subtitle{
    text-align: center;
    font-size:1.75rem;
    @media(min-width:$desktop-breakpoint){        
    margin-bottom: $ml-size;    
    font-size: $ml-size;
    padding-left:10rem;
    padding-right:10rem;
    }
}
.skillset__body{
    padding: 0;
    margin-top:$xl-size+$s-size;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media(max-width:$desktop-breakpoint){        
        flex-direction: column;
        margin-top:3.8rem;
    }
}
.skillset__item{
    padding:$s-size;
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    margin:0 $s-size;
    @media(max-width:$desktop-breakpoint){        
        flex-direction: row;
        padding: $sl-size;
        margin: $s-size auto;
    }
}
.skillset__item_title{
    font-weight: 600;
    margin-bottom: $s-size;
    @media(max-width:$desktop-breakpoint){        
        margin-bottom: $sl-size;
    }
}

.skillset__img{
    width:32.5%;
    align-self: center;
    margin-top: $s-size;
    margin-bottom: $s-size;
    @media(max-width:$desktop-breakpoint){     
        width:18%;
        height: 18%;
        margin: 0;
        margin-left: $sl-size;
        margin-top: $s-size;
        align-self: auto;
        }
}
.skillset__desc{
    text-align: center;
    margin: auto;
    @media(max-width:$desktop-breakpoint){        
        margin: auto $s-size !important;
        text-align: left;
        align-self: center;
        padding:$sl-size !important;
    }
}


