.bg { 
    background-image: url("/images/bg-f.jpg");
    height: 100%; 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    background: black;
    opacity: 0.3;
}
.cover-text{
 
    
    text-align: center;
    color: white;
    
}
#head{
    position: absolute;
    top: 42.5%;
    width:100%;
    text-align: center;
    transform: translate(0%, -50%);
    font-size: 6rem;
    font-weight: bold;
    text-shadow: 2px 2px #000;
    @media(max-width:$desktop-breakpoint){        
        font-size:4.4rem;
        top:42.5%;
    }
    padding: $m-size;
}
#foot{
    position: absolute;
    bottom: 20%;
    text-align: center;
    width: 100%;
    transform: translate(0%, -50%);
    font-size: 3.15rem;
    color: white;
    display: none;
    @media(max-width:$desktop-breakpoint){        
        font-size:2.5rem;
        bottom: 20%;
    }
    text-shadow: 2px 2px #000;
}
#continue{
    position: absolute;
    bottom: 8%;
    left:50%;
    text-align: center;
    transform: translate(-50%, -50%);
    font-size: $m-size;
    color: white;
    @media(max-width:$desktop-breakpoint){ 
        bottom: 7.5%;
        font-size:1.35rem;
    }
    
    padding: 1.5rem;
    &:hover{
        color: lightgray
    }
}


