.navbar{
    background: rgba(0,0,0,0.5);
    font-size:1.6rem;
    position: fixed;
    width: 100%;
    z-index: 70;
    @media(max-width:$desktop-breakpoint){        
        font-size:1.45rem;
        background: rgba(0,0,0,0.5);
    }
}
.nav-item{
    color:white !important;
    margin: 0.3rem 0.4rem;
    padding: 0.5rem $sl-size !important;
    &:hover{
        background: rgba(255,255,255,0.25);
        color:white !important;
        border-radius: 4%;
    }
    @media(max-width:$desktop-breakpoint){ 
        margin: 0.25rem $sl-size;      
        &:hover{
            border-radius: 1%;
        }
    }
    cursor: pointer;
}
.navbar-toggler{
    color:white !important;
    border-color:white !important;    
    padding: $sl-size $sl-size !important;
    font-size:1.5rem;
    margin: 0.5rem;
    margin-left:0.2rem;
    cursor: pointer;
}
#navbarNav{
    @media(max-width:$desktop-breakpoint){ 
        width: 50%;
    }
}
.navbar2{
    color:white;
    margin-right: 1.5rem;
    margin-left: 1.5rem;
    @media(max-width:$desktop-breakpoint){ 
        margin-right: 0rem;
        margin-left: 0.5rem;
    }
}
.navbar2 div{
    display:inline;
}
.menu-fab{
    color: white;
    margin:0.3rem;
    font-size:2.5rem;
    &:hover{
        color:lightgray;
    }
    @media(max-width:$desktop-breakpoint){ 
        margin:0.2rem;
    }
    cursor: pointer;
}
.active{
    background: rgba(255,255,255,0.25);
    color:white !important;
    border-radius: 4%;
    @media(max-width:$desktop-breakpoint){ 
        margin: 0.25rem $sl-size;       
        &:hover{
            border-radius: 1%;
        }
    }
}


