.web{
    margin: $ml-size $s-size;
    margin-top:$xl-size !important;
    box-shadow: 0px 0px 2rem #000000;
    background: -moz-linear-gradient(125deg, $gradient3-dark , $gradient3-light); /* ff3.6+ */
    background: -webkit-linear-gradient(125deg, $gradient3-dark , $gradient3-light); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(125deg, $gradient3-dark, $gradient3-light); /* opera 11.10+ */
    background: -ms-linear-gradient(125deg, $gradient3-dark, $gradient3-light); /* ie10+ */
    background: linear-gradient(125deg, $gradient3-dark , $gradient3-light); /* w3c */
    padding: $m-size $l-size;
    text-align: center;
    z-index: 50;
    position: relative;
    @media(max-width:$desktop-breakpoint){        
        margin:$l-size 2.4rem;
        padding: $m-size $ml-size;
    }
}

.web__title{
    font-weight:bold;
    font-size: $ml-size;
    color:white;
    padding-top: $s-size;
}

.web__subtitle{
    font-size: $m-size;
    color: white;
    margin-bottom: $m-size;
    padding: 0;
}

.web__item{
    padding:$ml-size;
    padding-top:$m-size; 
}
.web__item_title{
    font-weight: 600;
    font-size: 1.8rem;
    margin-top: $sl-size;
    margin-bottom: $s-size;
}

.web__card{
    height:100%;
    border: none;
    color: black;
    box-shadow: 0px 0px 0.5rem #000000;    
    border-radius: 0;
}

.web__card__footer{
    margin:1rem $s-size;
    margin-top:0.75rem;
    padding: $sl-size;    
    border:0.11rem solid $web-button-color;
    background: $web-button-color;
    color:white;        
    border-color:white;
    font-size: 1.4rem;
    font-weight: 500;
    &:hover{
        background: white;
        border-color: $web-button-color;
        color:$web-button-color;
        font-weight: bold;
    }
    
    cursor: pointer;
}
.web__card__footer2{
    margin:1rem $s-size;
    margin-top:0.75rem;
    padding: $sl-size;    
    border:0.11rem solid #78909C;
    background: #78909C;
    color:white;        
    border-color:white;
    font-size: 1.4rem;
    font-weight: 500;
    
    cursor: pointer;
}

.web__part__button{
    margin-top:$m-size !important;
    margin-bottom: $m-size !important;
    font-size:$m-size;
    color:white;
    border-color: white;
    &:hover{
      color:black;  
    }
    cursor: pointer;
}


