.contact{
    padding: $ml-size 0;
    background: url('/images/bg1.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.contact__title{
    text-align: center;
    font-weight:bold;
    font-size: $l-size;
    color:white;
    padding-top: $m-size;
    margin: 0 auto;
}

.contact__subtitle{
    text-align: center;
    font-size:1.75rem;
    @media(min-width:$desktop-breakpoint){        
    margin-bottom: $ml-size;    
    font-size: $ml-size;
    }
    margin-bottom: $ml-size;
    color: white;
    text-shadow: 1px 1px #000;
}
.contact__body{
    text-align: center;
    margin-top:$l-size;
    margin-bottom: $ml-size;
    color: white;
}
.contact--icons{
    margin: 0.5rem;
    margin-bottom: 1.5rem;
   
}
.con{
    display: inline;
}
.contact-fab{
    color: white;
    margin: 0.2rem;
    &:hover{
        color:lightgrey;
    }
    @media(max-width:$desktop-breakpoint){ 
        margin:0.2rem;
    }
    cursor: pointer;
}


