.horiz3{
    border-color: darken(lightgray,10%);
}
.horiz4{
    margin:0;
}
.horiz{
    width:$xl-size;
    border-bottom: 0.6rem solid #d32f2f;
}
.horiz7{
    width:40%;
    border-bottom: 0.2rem solid #d32f2f;
}
.horiz1{
    width:$xl-size;
    border-bottom: 0.6rem solid #FF8F00;
}
.horiz2{
    width:20%;
    border-bottom: 0.15rem solid white;
    @media(max-width:$desktop-breakpoint){        
        width:50%;
    }
}
.horiz5{
    float: left;
    width: 100%;
    border-color: white;
    margin-top: $l-size;
    margin-bottom: $ml-size;
}
.horiz6{
    border-color: darken(lightgray,10%);
    margin:0 !important;  
}
.test{
    border: 0.15rem solid white;
    border-radius:$m-size;
    padding:$sl-size $l-size;   
}

.card-img-top{
    border-radius:0;
}
.highlight{
    background: white;
    &:hover{
        background: lighten(lightgray,5%);
    }
}
.carousel-inner{
    width:100%;
    @media(max-width:$desktop-breakpoint){        
        width:99%;
    }
}


